hr {
	margin-bottom: 1rem;
	margin-top: 1rem;
}
.main {
	.left-part,
	.right-part {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-flow: column wrap;
		padding-top: 2rem;
	}
	.left-part {
		.profil-header {
			background-color: $background-theme;
			height: 15vh;
			width: 100%;
			z-index: -1;
			position: absolute;
			top: 0;
			border-radius: 0px 0px 20px 20px / 20%;
			box-shadow: 0 5px 0 0 rgba(122, 122, 122, 0.1);
		}
		.home-background {
			background-color: $background-grey;
			height: 60vh;
			width: 100%;
			z-index: -2;
			position: absolute;
			top: 0;
			box-shadow: 0 5px 0 0 rgba(122, 122, 122, 0.1);
		}
		.profil-pic {
			img {
				width: 150px;
				height: 150px;
				border-radius: 50%;
				border: 7px solid $background-grey;
			}
		}
		.profil-content {
			text-align: center;
			.profil-name {
				margin-bottom: 1rem;
			}
			.profil-description,
			.profil-about div span {
				font-weight: 200;
				font-size: 0.9em;

				a {
					text-decoration: none;
					color: $color-grey;
				}
			}
			.profil-about {
				display: grid;
				grid-template-columns: 160px 160px;
				grid-template-rows: 50px 50px;
				margin-top: 1rem;

				justify-content: space-evenly;
				justify-items: center;
				align-content: space-evenly;
				align-items: center;
			}
		}
		.content {
			.card {
				display: inline-grid;
				grid-template-columns: 1fr 1fr;
				grid-template-rows: 1fr 1fr;
				border-radius: 15px;
				border: 2px solid rgb(221, 221, 221);
				box-shadow: 0 0 0 0 rgba(122, 122, 122, 0.1);
				width: 325px;
				height: 325px;
				margin-top: 2rem;
				margin-bottom: 1rem;

				.subcard {
					display: flex;
					justify-content: space-evenly;
					align-items: center;
					flex-flow: column wrap;
					background-color: $background-theme;
					border-radius: 15px;
					margin: 10px;
					color: white;
					text-decoration: none;
				}
			}
			h1 {
				padding: 1rem;
				font-size: 1.15em;
				letter-spacing: 0.15em;
				text-transform: uppercase;
				text-align: center;
			}
		}
	}

	.right-part {
		.header {
			color: $color-theme;
			text-align: center;
			hr {
				margin-right: 30px;
				margin-left: 30px;
			}

			.job-title,
			.about .about-title,
			.experiences .experience-title,
			.formations .formation-title,
			.skills .skill-title {
				text-transform: uppercase;
				letter-spacing: 2px;
			}

			.about {
				margin: 2rem;
				text-align: left;

				.about-text {
					color: $color-grey;
					font-weight: 200;
					font-size: 0.9em;
				}
			}

			.experiences,
			.formations {
				margin: 1rem;
				.experience-title,
				.formation-title {
					margin-bottom: 1rem;
				}
				.card {
					width: 325px;
					margin-bottom: 1rem;
					border-radius: 15px;
					border: 2px solid rgb(221, 221, 221);
					box-shadow: 0 0 0 0 rgba(122, 122, 122, 0.1);

					.card-body {
						margin: 2rem;
						.card-title,
						.card-subtitle {
							color: $color-grey;
							text-transform: uppercase;
						}
						.card-subtitle {
							font-weight: 300;
							font-size: 1em;
						}
						.card-infos ul {
							text-align: left;
							margin-top: 1rem;
							list-style: none;
							li {
								color: $color-grey;
							}
							li:nth-child(2) {
								margin-top: 0.5rem;
								font-weight: 300;
								font-size: 1em;
							}
						}
					}
				}
			}

			.skills {
				margin: 1rem;
				.card {
					width: 325px;
					margin-top: 1rem;
					margin-bottom: 1rem;
					border-radius: 15px;
					border: 2px solid rgb(221, 221, 221);
					box-shadow: 0 0 0 0 rgba(122, 122, 122, 0.1);

					.skill-subtitle {
						margin-top: 1rem;
						letter-spacing: 2px;
						text-transform: uppercase;
						color: $color-grey;
					}

					.card-body {
						margin: 2rem;
						display: flex;
						flex-flow: wrap;
						justify-content: flex-start;
						align-items: center;
						.card-img {
							margin-right: 15px;
						}
						.card-title {
							color: $color-grey;
							margin-bottom: 0.5rem;
							text-align: left;
							text-transform: uppercase;
						}
						.progressbar {
							margin-bottom: 1rem;
							width: 75%;
							.progressbar-block {
								border-radius: 30px;
								background: $background-grey;
								box-shadow: 0 0 0 0 rgba(122, 122, 122, 0.1);
							}

							.progressbar-set {
								height: 10px;
								border-radius: 30px;
								background-image: linear-gradient(
									to bottom,
									rgba(255, 255, 255, 0.3),
									rgba(255, 255, 255, 0.05)
								);
								transition: 0.4s linear;
								transition-property: width, background-color;
							}

							.progressbar-moved .progressbar-set {
								background-color: $color-theme;
								animation: progressbarAnimation 4s;
							}

							@keyframes progressbarAnimation {
								0% {
									width: 0%;
									background-color: $color-theme;
								}
								100% {
									width: 100%;
									background-color: $color-theme;
								}
							}
						}
					}
				}
			}
		}
	}

	@media screen and (min-width: 1224px) {
		.main {
			margin-right: 500px;
		}
		.left-part {
			float: left;
		}
	}
}
